.ProjectImg {
    width: 100%;
    height: 280px;
    background-color: #999;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    position: relative;

    &::after {
      content: '';
      background-color: black;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0.4;
    }
}

.ProjectLink {
  z-index: 1;
  background-color: rgba(248,249,250,.7);
  width: 200px;
  padding: 10px 0;
  text-align: center;
  text-decoration: none;
  font-size: 1.2rem;
  color: #343a40;
}

.ProjectWrapper {
  width: 400px;
  li {
    color: white;
  }
}